.sidebar {
  display: inline-block;
  height: 100%;
  width: 70px;
  background: #0A1126;
}

.sidebar ul {
  display: inline-block;
  list-style-type: none;
  padding: 0;
  margin: 10px 0;
  width: 100%;
}

.sidebar ul li {
  display: inline-block;
  width: 100%;
  box-sizing: border-box;
  padding: 10px;
}

.sidebar ul li a {
  display: inline-block;
  width: 50px;
  height: 50px;
  padding: 0;
  border-radius: 15px;
  outline: none;
  color: #fff;
  font-size: 22px;
  background: #202E3A;
  text-align: center;
  line-height: 50px;
  transition: background .15s ease-out;
}

.sidebar ul li a.active, .sidebar ul li a:hover {
  background: #516575;
}

.sidebar ul li a.moveActive, .sidebar ul li a.zoomActive {
  background: #a75713;
}