.profile {
  width: 25%;
  max-width: 380px;
  margin: 20px 10px;
  cursor: pointer;
  background: #000;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  position: relative;
}

.profile img {
  width: 100%;
}

.profile .info {
  position: absolute;
  width: 100%;
  bottom: 0;
  background-image: linear-gradient(transparent, #000);
  padding: 20px 0;
}

.profile .info h2 {
  margin: 0;
  display: inline-block;
  color: #fff;
  font-size: 25px;
  padding: 10px 20px;
  width: 100%;
  text-shadow: 0 0 10px #000;
  margin-bottom: 15px;
  box-sizing: border-box;
  font-weight: 400;
}

.profile .info .actions {
  display: flex;
  width: 100%;
  justify-content: center;
}

.profile .info .actions button {
  background: transparent;
  width: 60px;
  height: 60px;
  border-radius: 30px;
  border: 3px solid;
  font-size: 23px;
  margin: 0 10px;
  cursor: pointer;
  padding-top: 5px;
}

.profile .info .actions button.dislike {
  color: #fe4a69;
}

.profile .info .actions button.like {
  color: #22eabc;
}