.login-page {
  display: flex;
  width: 100%;
  height: 100%;
  align-items: center;
  justify-content: center;
  padding-left: 35px;
}

.login-page .box h1 {
  color: #fff;
  font-size: 50px;
  width: center;
  text-align: center;
}

.login-page .box input {
  vertical-align: top;
  display: inline-block;
  padding: 8px 10px;
  outline: none;
  font-size: 20px;
  width: 387px;
  border: none;
  background: #c6c7c9;
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
}

.login-page .box button {
  vertical-align: top;
  display: inline-block;
  background: #50474E;
  border: none;
  color: #fff;
  padding: 5px 15px;
  cursor: pointer;
  font-size: 23px;
  border: solid #5d575c 1px;
  border-left: none;
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
}