.likes {
  display: flex;
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  padding: 10px;
  flex-wrap: wrap;
  overflow-y: auto;
  justify-content: center;
}

.likes .like {
  width: 25%;
  max-width: 380px;
  margin: 20px 10px;
  cursor: pointer;
  background: #000;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  position: relative;
}

.likes .like img {
  width: 100%;
}

.loading {
  width: 100%;
  text-align: center;
  color: #b5b5b5;
  font-size: 40px;
  padding: 10px 0;
}
