.viewer {
  position: fixed;
  background: rgba(0,0,0,.7);
  width: calc(100% - 70px);
  height: 100%;
  z-index: 3;
  top: 0;
  display: flex;
  flex-wrap: wrap;
  left: 70px;
}

.viewer .preview {
  display: flex;
  flex-grow: 1;
  align-items: center;
  height: 100%;
  overflow: hidden;
}

.viewer .preview .next, .viewer .preview .prev {
  flex-grow: 1;
  height: 40%;
  color: #fff;
  background: transparent;
  width: 60px;
  cursor: pointer;
  border: none;
  font-size: 30px;
}

.viewer .preview .prev {
  left: 0;
}

.viewer .preview .next {
  right: 0;
}

.viewer .preview img {
  max-width: 90%;
  max-height: 90%;
  object-fit: contain;
}

.viewer .info {
  width: 500px;
  background: #fff;
  display: flex;
  flex-direction: column;
  position: relative;
  height: 100%;
}

.viewer .info .close {
  position: absolute;
  background: transparent;
  border: none;
  color: #000;
  font-size: 28px;
  cursor: pointer;
  top: 10px;
  right: 10px;
}

.viewer .info h1 {
  display: inline-block;
  margin: 0;
  padding: 15px;
}

.viewer .info .distance {
  display: inline-block;
  margin: 0;
  padding: 0 15px;
}

.viewer .info p {
  width: 100%;
  margin: 0;
  padding: 20px;
  display: inline-block;
  box-sizing: border-box;
}

.viewer .gallery {
  width: calc(100% - 40px);
  flex-grow: 1;
  background-color: #cacaca;
  margin: 0 20px 20px;
  padding: 10px;
  box-sizing: border-box;
  overflow-y: auto;
}

.viewer .gallery img {
  width: calc(20% - 10px);
  margin: 5px;
  cursor: pointer;
}

.viewer .gallery img.active {
  outline: 4px solid #ff5e00;
}
