html, body {
  height: 100%;
}

body {
  margin: 0;
  font-family: 'Roboto', sans-serif;
  background: #383D46;
  overflow-y: hidden;
}

#root, .tash-app {
  width: 100%;
  height: 100%;
  display: inline-block;
}

.page {
  display: inline-block;
  height: 100%;
  vertical-align: top;
  width: calc(100% - 70px);
}